.tweetBox{
    padding-bottom: 10px;
    border-bottom: 8px solid var(--twitter-color);
    padding-right: 10px;
}

.tweetBox > form{
    display: flex;
    flex-direction: column;
}

.tweetBox_input{
    padding: 20px;
    display: flex;
}

.tweetBox_input > input{
    flex: 1;    /* give as much amount as it want*/
    margin-left: 20px;
    font-size: 20px;
    border: none;
    outline: none;
}

.tweetBox_imageInput{
    border: none;
    padding: 10px;
    outline: none;
}

.tweetBox_tweetButton{
    background-color: var(--twitter-color) !important;
    border: none;
    color: white !important;
    font-weight: 900 !important;    /*override all the material ui properties*/
    text-transform: inherit !important;
    border-radius: 30px !important;
    height: 40px !important;
    margin-top: 20px !important;
    width: 80px !important;
    margin-left: auto !important;      /*to shift to the right*/
}